import back from '../../components/back.vue';
export default {
	data() {
		return {
			pageNameFlag:1,
			showFlag:0,
			pageName:'设置',
		}
	},
	methods: {
		exit(){
			uni.showModal({
			    title: '提示',
			    content: '确认退出登录',
			    success: function (res) {
			        if (res.confirm) {
						uni.removeStorageSync("account")
						uni.removeStorageSync("password")
			            uni.reLaunch({
			            	url:'../login/login'
			            })
			        } else if (res.cancel) {
			            console.log('用户点击取消');
						return;
			        }
			    }
			});
		}
	},
	components:{
		back
	}
}
